const Java = () => {
  return <img src={javaLogo} data-tip data-for="java" alt="java"></img>;
};

const K8s = () => {
  return (
    <img
      data-tip
      data-for="k8s"
      alt="kubernetes"
      style={{ height: "50%" }}
      src={k8sLogo}
    ></img>
  );
};

const Gcp = () => {
  return (
    <img
      data-tip
      data-for="gcp"
      alt="google cloud platform"
      style={{ height: "120%" }}
      src={gcp}
    ></img>
  );
};

const Cypress = () => {
  return (
    <img
      data-tip
      data-for="cypress"
      alt="cypress"
      style={{ height: "100%" }}
      src={cypress}
    ></img>
  );
};

const CircleCI = () => {
  return (
    <img
      data-tip
      data-for="circleci"
      alt="circleci"
      style={{ height: "120%" }}
      src={circleci}
    ></img>
  );
};

const GraphQL = () => {
  return (
    <div style={{ height: "100%" }}>
      <img
        data-tip
        data-for="graphql"
        alt="graphql"
        style={{ height: "100%" }}
        src={graphql}
      ></img>
    </div>
  );
};

export const Tooltips = () => (
  <div>
    <ReactTooltip id="graphql" type="info" effect="solid">
      <span>GraphQL</span>
    </ReactTooltip>
    <ReactTooltip id="react" type="info" effect="solid">
      <span>React</span>
    </ReactTooltip>
    <ReactTooltip id="js" type="info" effect="solid">
      <span>Javascript</span>
    </ReactTooltip>
    <ReactTooltip id="nodejs" type="info" effect="solid">
      <span>NodeJS</span>
    </ReactTooltip>
    <ReactTooltip id="cypress" type="info" effect="solid">
      <span>Cypress</span>
    </ReactTooltip>
    <ReactTooltip id="docker" type="info" effect="solid">
      <span>Docker</span>
    </ReactTooltip>
    <ReactTooltip id="java" type="info" effect="solid">
      <span>Java</span>
    </ReactTooltip>
    <ReactTooltip id="gcp" type="info" effect="solid">
      <span>Google Cloud Platform</span>
    </ReactTooltip>
    <ReactTooltip id="k8s" type="info" effect="solid">
      <span>Kubernetes</span>
    </ReactTooltip>
    <ReactTooltip id="circleci" type="info" effect="solid">
      <span>CircleCI</span>
    </ReactTooltip>
  </div>
);

export const techLogos = {
  node: <Nodejs />,
  react: <ReactLogo />,
  docker: <Docker />,
  js: <Javascript />,
  java: <Java />,
  k8s: <K8s />,
  gcp: <Gcp />,
  cypress: <Cypress />,
  circleci: <CircleCI />,
  graphql: <GraphQL />,
};



